import { useState, useMemo } from 'react';

export function useFilters(initial?: { selectedClient?: string; selectedStatus?: string }) {
  const [selectedClient, setSelectedClient] = useState<string>(initial?.selectedClient ?? '');
  const [selectedStatus, setSelectedStatus] = useState<string>(initial?.selectedStatus ?? '');

  const isActive = useMemo(() => (selectedClient.trim() !== '' || selectedStatus.trim() !== ''), [selectedClient, selectedStatus]);
  const reset = () => {
    setSelectedClient('');
    setSelectedStatus('');
  };

  const countLabel = (total: number, filtered: number) => (isActive ? `${filtered}ê°œ` : `ì´ ${total}ê°œ`);

  return {
    selectedClient,
    selectedStatus,
    setSelectedClient,
    setSelectedStatus,
    reset,
    isActive,
    countLabel,
  };
}
